import Vue from 'vue'
import Vuex from 'vuex'

//Vue
Vue.use(Vuex)

//VuexStore()
export default new Vuex.Store({
  //
  state: {
    // 
    isLogin: localStorage.getItem('isLogin') || 0,
    // 
    info: JSON.parse(localStorage.getItem('info')) || {},

  },
  mutations: {
    // 
    loginMutation(state, payload) {
      //
      state.isLogin = 1;
      //info
      state.info = payload;
    },
    // 
    logoutMutation(state) {
      state.isLogin = 0;
      state.info = {};
      localStorage.clear();
    },
  },
  actions: {},
  modules: {}
})